<script>
  import { createEventDispatcher } from "svelte";
  const dispatch = createEventDispatcher();
  export let movie;

  const removeDetail = () => {
    console.log("Clicked removedetail");
    dispatch("removeDetail");
  };
</script>

<style>
  .info {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    background-color: #333;
    opacity: 1;
    color: white;
    border: 10px solid white;
    z-index: 4;
    padding: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .content {
    padding-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    background: #fff;
    color: #333;
    height: 100vh;
    max-width: 100rem;
    border-radius: 20px;
  }

  h2 {
    margin-top: 10px;
    color: #222;
    margin: 3rem 0;
  }
  .year {
    font-weight: bold;
  }

  .poster {
    border: 1px solid #ddd;
    padding: 5px;
    margin: 0 2rem;
  }

  .twocols {
    display: grid;
    grid-template-columns: 300px 1fr;
    grid-gap: 20px;
    text-align: left;
  }

  @media (max-width: 640px) {
    .twocols {
      grid-template-columns: 1fr;
    }
    .poster {
      display: none;
    }
  }

  .plot {
    font-style: italic;
  }

  .column {
    padding: 10px;
  }
</style>

{#if movie}
  <div class="wrapper">
    <div class="info" on:click={() => removeDetail()}>
      <div class="content">
        <h2>{movie.Title}</h2>
        <p class="year">{movie.Year}</p>
        <div class="twocols">
          <div class="column">
            <img src={movie.Poster} alt="" class="poster" />
          </div>
          <div class="column">
            <strong>Plot</strong>
            <p class="plot">{movie.Plot}</p>
            <strong>Cast</strong>
            <p>{movie.Actors}</p>
            <p class="imdb">
              IMDB rating:
              <strong>{movie.imdbRating}</strong>
            </p>
            {#if movie.Awards}
              <strong>Awards</strong>
              <p>{movie.Awards}</p>
            {/if}
          </div>

        </div>

      </div>
    </div>
  </div>
{/if}
