<script>
  import { createEventDispatcher } from "svelte";
  const dispatch = createEventDispatcher();
  export let movie;

  const onCardClick = id => {
    dispatch("detail", id);
  };
</script>

<style>
  .card {
    background: white;
    padding: 20px;
    border-radius: 6px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
  }

  .card:hover {
    background: #eee;
    padding: 20px;
    border-radius: 6px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  }

  .card h4 {
    font-weight: 800;
  }
</style>

<div class="card" on:click={() => onCardClick(movie.imdbID)}>

  <h4>{movie.Title}</h4>
  <div class="description">{movie.Year}</div>
  <img src={movie.Poster || 'https://picsum.photos/400/300'} alt="" />
</div>
