<script>
  import { createEventDispatcher } from "svelte";
  const dispatch = createEventDispatcher();

  let searchString;
  let errorString = "";
  let valid = false;

  const submitHandler = () => {
    valid = true;
    // validate question
    if (searchString.trim().length < 2) {
      valid = false;
      errorString = "Search string must be at least 2 characters long";
    }

    if (valid) {
      console.log("Valid, dispatching search with:", searchString);
      dispatch("search", searchString);
    }
  };
</script>

<style>
  form {
    width: 500px;
    margin: 0 auto;
    text-align: center;
  }

  .form-field {
    margin: 18px auto;
  }

  input {
    width: 100%;
    border-radius: 6px;
  }

  label {
    margin: 10px auto;
    text-align: left;
  }

  .error {
    font-weight: bold;
    color: #d91b42;
  }
</style>

<div class="form" on:submit|preventDefault={submitHandler}>
  <form class="search">
    <label for="search" />
    <div class="form-field">
      <input
        type="text"
        id="search"
        bind:value={searchString}
        autocomplete="off" />
      <div class="error">{errorString}</div>
    </div>
    <button class="button button-outline">Search movies</button>
  </form>

</div>
