<script>
  import { onMount } from "svelte";
  import Navbar from "./components/Navbar.svelte";
  import Search from "./components/Search.svelte";
  import MovieList from "./components/MovieList.svelte";
  import MovieInfo from "./components/MovieInfo.svelte";
  import Footer from "./components/Footer.svelte";

  //const apiUrl = "http://www.omdbapi.com/?t=bad&apikey=e14c5aa7";
  const apiUrl = "https://www.omdbapi.com/?apikey=e14c5aa7&s=bad";
  const apiUrlBase = "https://www.omdbapi.com/?apikey=e14c5aa7&s=";
  const apiDetailUrlBase =
    "https://www.omdbapi.com/?apikey=e14c5aa7&plot=full&i=";

  let movies = [];
  let movie = null;
  let noMovies = false;

  onMount(async () => {
    fetch(apiUrl)
      .then(response => response.json())
      .then(json => {
        console.log(json);
        movies = json.Search;
      });
  });

  const handleSearch = e => {
    let query = e.detail;
    let queryUrl = apiUrlBase + query;
    fetch(queryUrl)
      .then(response => response.json())
      .then(json => {
        console.log(json);
        if (!json.Search) {
          noMovies = true;
          console.log("No results!");
        }
        movies = json.Search;
        noMovies = false;
      });
  };

  const handleCardClick = e => {
    console.log("Event made it to the top app.svelte!");

    let detailQuery = apiDetailUrlBase + e.detail;
    fetch(detailQuery)
      .then(response => response.json())
      .then(json => {
        console.log(json);
        movie = json;
      });
  };

  const removeMovie = e => {
    movie = null;
  };
</script>

<style>
  .ultra {
    color: fuchsia;
  }
  main {
    text-align: center;
    padding: 1em;
    max-width: 240px;
    margin: 0 auto;
  }

  @media (min-width: 640px) {
    main {
      max-width: none;
    }
  }

  .no-results {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
  }
</style>

<main>
  <h1>Freethrow Movie Search</h1>

  <p>
    By yours truly
    <a href="https://www.freethrow.rs">freethrow.rs</a>
    a simple
    <span class="ultra">warp speed</span>
    movie search app, built with Svelte.js.
  </p>
  <Search on:search={handleSearch} />

  {#if movie}
    <MovieInfo {movie} on:removeDetail={() => removeMovie()} />
  {/if}

</main>

<section id="results">
  {#if movies}
    <MovieList {movies} on:detail={handleCardClick} />
  {:else}
    <div class="no-results">
      <h4>
        Sadly, we couldn't find anything with
        <span class="ultra">that string</span>
      </h4>
    </div>
  {/if}
</section>
<Footer />
